
import { dataValidation } from "./datavalidation";
export const loadMasterData = () => {
    Excel.run(async function (context) {
      try {
        const workbook = context.workbook;
        const ProductDataWorksheet = workbook.worksheets.getItem("ProductsData");
        ProductDataWorksheet.activate();
        const currentWorkSheet = workbook.worksheets.getActiveWorksheet();
        const response = await fetch("https://dummyjson.com/products");
        const responseData = await response.json();
        const productsData = responseData.products.map((product) => {
          const newProduct = { ...product };
          delete newProduct.images;
          return Object.values(newProduct);
        });
        if (productsData.length > 0) {
          const rowCount = productsData.length;
          const columnCount = productsData[0].length;
          const endColumn = String.fromCharCode(65 + productsData[0].length - 1);
          const range = currentWorkSheet.getRange(`A1:${endColumn}${rowCount}`);
          range.values = productsData;
          await context.sync();
          dataValidation(rowCount)
          await context.sync();
          return;
        }
        console.log("Products Data length is Zero");
      } catch (error) {
        console.log(error);
      }
    });
  };