import { async } from "regenerator-runtime"

export const dataValidation=(rowCount)=>{
    Excel.run(async (context)=>{
        const productsDatasheet=context.workbook.worksheets.getItem('ProductsData')
        const workingssheet=context.workbook.worksheets.getItem("Workings")
        const myrange=workingssheet.getRange("A:A")
        myrange.dataValidation.clear()
        const productNameRange=workingssheet.getRange("A:A")
        await context.sync()
        const productTitleRange=productsDatasheet.getRange("B:B").getUsedRange()
        productTitleRange.load('values')
        await context.sync()
        productNameRange.dataValidation.rule={
        list:{
            inCellDropDown:true,
            source:productTitleRange
        }
       }
       workingssheet.onSelectionChanged.add(async(args)=>{
        const selectedRange=args.address
        if(selectedRange[0]=="A"){
            console.log("Thier is change in the A column")
            console.log("The Range",selectedRange)
            console.log(typeof(selectedRange))
            Excel.run(async (context)=>{
                const sheet=context.workbook.worksheets.getActiveWorksheet()
                const cellAddress=selectedRange.toString()
                const range=sheet.getRange(cellAddress)
                range.load('address')
                await context.sync()
                console.log("Selected address is",range.address)
                range.load("values")
                await context.sync()
                console.log("The Value Selected is",range.values)
            })
        }
       
      
       })
      
    })
}